<template>
    <div>
        <h3 class="px10 fwnormal">Elpriset idag i SE</h3>
        <canvas id="dieChart" class="timeChart" width="380" height="400" ref="dieChart"></canvas>
        <h3 class="px10 fwnormal">Månadspriser i SE</h3>
        <canvas id="lieChart" class="timeChart" width="380" height="400" ref="lieChart"></canvas>
        <div class="tick-wrapper">
            <!--<div class="tick">
                <i class="icofont-arrow-up" style="color: #ea161d;"></i>
                <span class="tick-title">Högsta priset</span>
                <span class="tick-value">{{ highestValue }} öre</span>
                <span class="tick-date">kl {{ highestDate }}:00</span>
            </div>
            <div class="tick">
                <i class="icofont-arrow-down" style="color: #66ea16;"></i>
                <span class="tick-title">Lägsta priset</span>
                <span class="tick-value">{{ lowestValue }} öre</span>
                <span class="tick-date">kl {{ lowestDate }}:00</span>
            </div>-->
        </div>
    </div>
</template>

<script>
import { Chart } from "chart.js";

export default {
    name: "AboutUs",
    hourlyChart: null,
    weeklyChart: null,
    data() {
        return {
            datasets: [],
            datasets2: [],
            labels: [],
            highestValue: null,
            highestDate: null,
            lowestValue: null,
            lowestDate: null,
        }
    },
    methods: {
        async fetchJSON(url, label, color, chartCanvas, onDataset) {
            // Fetch the JSON file and create a new dataset object
            const response = await fetch(url);
            const data = await response.json();
            const labels = data.map((item) => item.date).reverse();
            const values = data.map((item) => item.value).reverse();

            const canvas = document.getElementById(chartCanvas).getContext('2d');
            const gradient = canvas.createLinearGradient(0, 0, 0, 400);

            gradient.addColorStop(0, color + `,0.5)`);
            gradient.addColorStop(1, color + `,0)`);

            this.gradient = gradient;

            const dataset = {
                label: label,
                data: values,
                backgroundColor: this.gradient,
                borderColor: color + ")",
                borderWidth: 3,
                tension: 0.2
            };

            this.labels = labels; // Set the labels property to the labels variable
            // Add the dataset object to the datasets array
            this[onDataset].push(dataset);
        },
        async createChart(chartName, canvasName, timeData, inDataset) {
            // Fetch the JSON files and add them as datasets to the chart
            for (const item of timeData) {
                await this.fetchJSON(item.url, item.label, item.color, canvasName, inDataset);
            }

            // Create the Chart.js chart using the canvas element and the datasets array
            this[chartName] = new Chart(this.$refs[canvasName], {
                type: "line",
                data: {
                    labels: this.labels,
                    datasets: this.datasets
                },
                options: {
                    elements: {
                        point: {
                            radius: 0
                        }
                    },
                    legend: {
                        labels: {
                            fontColor: "#c5b9bb",
                            fontSize: 15,
                            usePointStyle: true,
                            boxWidth: 5
                        }
                    },
                    scales: {
                        yAxes: [
                            {
                                gridLines: {
                                    color: "#353545"
                                },
                                ticks: {
                                    beginAtZero: true,
                                    fontColor: "#c5b9bb",
                                },
                            },
                        ],
                        xAxes: [
                            {
                                gridLines: {
                                    //display: false,
                                    color: "#353545"
                                },
                                ticks: {
                                    //display: false,
                                    beginAtZero: true,
                                    fontColor: "#c5b9bb",
                                },
                            },
                        ],
                    },
                }
            });
        }
    },
    mounted() {
        const hourly = [
            { url: "https://a.storyblok.com/f/56231/x/8d1812c2ec/se4.json", label: "SE4", color: "rgba(234, 22, 142" },
            { url: "https://a.storyblok.com/f/56231/x/b39e5c8765/se3.json", label: "SE3", color: "rgba(22, 234, 167" },
            { url: "https://a.storyblok.com/f/56231/x/d5b71eb7dc/se2.json", label: "SE2", color: "rgba(187, 22, 234" },
            { url: "https://a.storyblok.com/f/56231/x/76dadcabe8/se1.json", label: "SE1", color: "rgba(234, 127, 22" }
        ]
        const weekly = [
            { url: "https://a.storyblok.com/f/56231/x/8d1812c2ec/se4.json", label: "SE4", color: "rgba(234, 22, 142" },
            { url: "https://a.storyblok.com/f/56231/x/b39e5c8765/se3.json", label: "SE3", color: "rgba(22, 234, 167" },
            { url: "https://a.storyblok.com/f/56231/x/d5b71eb7dc/se2.json", label: "SE2", color: "rgba(187, 22, 234" },
            { url: "https://a.storyblok.com/f/56231/x/76dadcabe8/se1.json", label: "SE1", color: "rgba(234, 127, 22" }
        ]
        this.createChart('hourlyChart','dieChart', hourly, 'datasets');
        this.createChart('hourlyChart','lieChart', weekly, 'datasets2');
    },
};
</script>

