  mounted() {
    // Init chart for kwh usage
    this.chart = new Chart(this.$refs.pieChart, {
      type: "doughnut",
      data: this.piedata,
      options: {
        cutoutPercentage: 70,
        elements: {
          arc: {
            borderWidth: 0,
            borderColor: "#131114",
          },
        },
        legend: {
          display: false,
        },
      },
    });
    // Check if there are stored values in local storage
    if (localStorage.getItem("items")) {
      // If there are, set the component's items data to the stored values
      this.items = JSON.parse(localStorage.getItem("items"));
    }
  },
  computed: {
    totalConsumption() {
      return this.items
        .reduce((acc, item) => acc + item.value * item.hours * item.kwh, 0)
        .toFixed(2);
    },
    piedata() {
      return {
        labels: this.items.map((item) => item.name),
        cutoutPercentage: 10,
        datasets: [
          {
            data: this.items.map((item) => item.value * item.hours * item.kwh),
            backgroundColor: this.items.map((item) => item.color)
          },
        ],
      };
    },
  },
  methods: {
    setDay() {
      this.activeTab = 0;
      this.day = 1;
      this.time = "dag";
      this.totalConsumption();
      this.piedata();
    },
    setWeek() {
      this.activeTab = 1;
      this.day = 7;
      this.time = "vecka";
      this.totalConsumption();
      this.piedata();
    },
    setMonth() {
      this.activeTab = 2;
      this.day = 31;
      this.time = "månad";
      this.totalConsumption();
      this.piedata();
    },
    setYear() {
      this.activeTab = 3;
      this.day = 365;
      this.time = "år";
      this.totalConsumption();
      this.piedata();
    },
    addItem(item) {
      // Add the new item to the items array
      this.items.push(item);

      // Update the chart to reflect the changes
      this.chart.data.datasets[0].data = this.items.map(item => item.value.toFixed(3));
      this.chart.data.datasets[0].backgroundColor = this.items.map(item => item.color);
      this.chart.update();
      // Close popup TODO: fix error where 
      this.closePopup();
    },
    removeItem(index) {
      this.items.splice(index, 1);

      // Update the chart to reflect the changes
      this.chart.data.datasets[0].data = this.items.map(item => item.value.toFixed(3));
      this.chart.data.datasets[0].backgroundColor = this.items.map(item => item.color);
      this.chart.update();
    },
    openPopup() {
      // Get the popup element
      const popup = document.getElementById("popup");
      const body = document.body;
      // Show the popup
      popup.style.display = "block";
      body.style.overflow = "hidden";
    },
    closePopup() {
      // Get the popup element
      const popup = document.getElementById("popup");
      const body = document.body;
      // Show the popup
      popup.style.display = "none";
      body.style.overflow = "auto";
    }
  },
  watch: {
    // Watch the items data for changes
    items: {
      deep: true,
      handler() {
        if (this.chart) {
          // Update the chart data
          this.chart.data = this.piedata;
          this.chart.update();
        }
        // When the data changes, store the new values in local storage
        localStorage.setItem("items", JSON.stringify(this.items));
      },
    },
  },
};
